export default {
  name: 'App',
  watch: {
    '$store.getters.disconnect': function $storeGettersDisconnect() {
      console.log(this.$store.getters.disconnect);
      var that = this; // true

      if (this.$store.getters.disconnect) {
        this.$dialog.alert({
          message: ''
        }).then(function () {
          window.sessionStorage.removeItem('name'); // that.$router.replace("/sessioned");

          that.$router.push('/blank');
          that.$store.dispatch('actionsDisconnect', false);
        });
      }
    }
  }
};