export default {
  install: function install(Vue) {
    // sweetalert2 
    swal.setDefaults({
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: 'rgb(140,212,245)',
      cancelButtonColor: 'rgb(193,193,193)'
    }); // 

    Vue.swal = swal; // 

    Vue.prototype.$swal = swal;
  }
};