import "core-js/modules/es6.function.name";
import rootStore from "../index";
import router from "../../router/index";
import Toast from 'vant';
var state = {
  chatRoomList: []
};
var mutations = {
  PUSH_MSG: function PUSH_MSG(state, msg) {
    var obj = {};
    var msgContent = {};
    console.info('', msg);

    if (msg.length) {
      msgContent = msg[0];
    } else {
      msgContent = msg;
    }

    console.info('msgContent', msgContent);

    switch (msgContent.type) {
      case 'notification':
        // 
        if (msgContent.attach.type == 'memberEnter') {
          var _fromCustom = JSON.parse(msgContent.fromCustom);

          obj = {
            type: 'notification',
            name: _fromCustom.userName
          };
          rootStore.getters.peopleList.push(obj);
        }

        break;

      case 'text':
        // 
        if (msgContent.custom) {
          var _obj = JSON.parse(msgContent.custom);

          console.info('obj', _obj);

          if (_obj.from.cid != rootStore.getters.channelInfo.cid) {
            return;
          }
        }

        var ifTeller = false;
        var ifAssistant = false;
        var fromCustom = JSON.parse(msgContent.fromCustom);
        var name = fromCustom.userName;

        if (msgContent.from == rootStore.getters.channelInfo.tellerId) {
          ifTeller = true;
        }

        if (fromCustom.identity == 1) {
          ifAssistant = true;
        }

        obj = {
          type: 'text',
          name: name,
          content: msgContent.text,
          ifTeller: ifTeller,
          userId: msgContent.from,
          ifAssistant: ifAssistant
        };
        console.info('obj_', obj);
        state.chatRoomList.push(obj);
        break;

      case 'custom':
        // 
        var content = JSON.parse(msgContent.content);
        console.info('content', content);

        if (content.from.cid != rootStore.getters.channelInfo.cid) {
          return;
        } // 


        if (content.type == 0) {
          obj = {
            type: 'product',
            name: content.from.userName,
            goodName: content.data.goodName,
            goodCoverUrl: content.data.goodCoverUrl,
            jumpLink: content.data.jumpLink
          };
          rootStore.commit('channel/SET_PRODUCTINFO', obj); // state.chatRoomList.push(obj)
          // 
        } else if (content.type == 1) {
          obj = {
            type: 'gift',
            name: content.from.userName,
            coverName: content.data.name,
            coverUrl: content.data.coverUrl
          };
          state.chatRoomList.push(obj); // 
        } else if (content.type == 1400) {
          console.info('');

          if (content.state == 1) {
            rootStore.commit('channel/SET_STOPFLOW', false);
          } else {
            rootStore.commit('channel/SET_STOPFLOW', true);
          }
        } else if (content.type == 300) {
          // 
          rootStore.dispatch('msgs/banned', content);

          if (content.data.isMuted == 1) {
            if (content) {
              obj = {
                type: 'banned',
                name: content.data.userName,
                adminName: content.from.userName,
                userId: content.data.userId,
                identity: content.from.identity
              };
            }

            state.chatRoomList.push(obj);
          } // 

        } else if (content.type == 1300) {
          rootStore.dispatch('msgs/wholeBanned', content);
        } else if (content.type == 900) {
          // 
          rootStore.commit('channel/SET_REDBAGFLOW', true);
          rootStore.commit('channel/SET_REDBAGINFO', content.data);
        } else if (content.type == 5) {
          obj = {
            type: 'product',
            goodCoverUrl: content.data.goodCoverUrl,
            goodName: content.data.goodName,
            goodTitle: content.data.goodTitle,
            jumpLink: content.data.jumpLink
          };
          state.chatRoomList.push(obj); // 
        } else if (content.type == 200) {
          rootStore.commit('channel/SET_OVERFLOWINFO', true); // 
        } else if (content.type == 1200) {
          var num = content.count;
          rootStore.commit('channel/UPDATE_LIKEHARTNUM', num); // 
        } else if (content.type == 1500) {
          rootStore.commit('channel/SET_INTRODUCTORY', content.content); // 
        } else if (content.type == 1600) {
          rootStore.commit('channel/SET_NOTICE', content.notice); // 
        } else if (content.type == 1000) {
          rootStore.dispatch('channel/getSensitiveWord', {
            tellerId: rootStore.getters.channelInfo.tellerId
          });
        } else if (content.type == 1700) {
          console.info('content', content);
          rootStore.commit('channel/UPDATE_ADVANCESTARTLIVE', true);
        } else if (content.type == 1701) {
          // 
          console.info('content', content);
          rootStore.commit('channel/SET_VIRTUALUSERNUM', content.virtualUserNum);
        } else if (content.type == 1702) {
          // 
          rootStore.commit('channel/CLEAR_VIRTUALUSERNUM');
        } else if (content.type == 400) {
          rootStore.commit('channel/SET_OUTROOM', true);
        }

        break;
    }

    console.info('chatRoomList', state.chatRoomList);
  },
  CLEAR_CHATROOMLIST: function CLEAR_CHATROOMLIST(state) {
    state.chatRoomList = [];
    console.info('state.chatRoomList', state.chatRoomList);
  }
};
var actions = {
  // 
  banned: function banned(context, content) {
    console.info('content111', content);
    var userId = JSON.parse(window.localStorage.getItem('loginInfo')).userId;

    switch (content.data.isMuted) {
      case '0':
        // --
        if (content.data.userId == userId) {
          console.info('');
          rootStore.commit('channel/SET_JYTYPE', false);
        }

        break;

      case '1':
        // --
        if (content.data.userId == userId) {
          rootStore.commit('channel/SET_JYTYPE', true);
        }

        break;
      // case '2':
      //   // 
      //   rootStore.commit('channel/SET_JYTYPE', true)
      //   break
      // case '3':
      //   // 
      //   rootStore.dispatch('channel/getBanned', {
      //     userId: userId,
      //     tellerId: rootStore.getters.channelInfo.tellerId
      //   }).then((res) => {
      //     console.info('', res)
      //     if (res.data === 1) {
      //       rootStore.commit('channel/SET_JYTYPE', true)
      //     } else {
      //       rootStore.commit('channel/SET_JYTYPE', false)
      //     }
      //   })
      //   break
    }
  },
  // 
  wholeBanned: function wholeBanned(context, content) {
    var userId = JSON.parse(window.localStorage.getItem('loginInfo')).userId;

    if (content.state == 1) {
      rootStore.commit('channel/SET_JYTYPE', true);
    } else {
      rootStore.dispatch('channel/getBanned', {
        userId: userId,
        tellerId: rootStore.getters.channelInfo.tellerId
      }).then(function (res) {
        console.info('', res);

        if (res.data === 1) {
          rootStore.commit('channel/SET_JYTYPE', true);
        } else {
          rootStore.commit('channel/SET_JYTYPE', false);
        }
      });
    }
  }
};
export default {
  namespaced: true,
  actions: actions,
  mutations: mutations,
  state: state
};