import router from "./index";
import store from "../store/index";
router.beforeEach(function (to, from, next) {
  console.log('router start', new Date().getTime());
  console.log('router from', from);
  console.log('router to', to); // const fromPath = from.path
  // const toPath = to.path
  // const cid = sessionStorage.getItem('')

  if (store.getters.jyType === null) {
    next();
    return;
  } else {
    var jyType = Boolean(parseInt(localStorage.getItem('jyType')));
    store.commit('setjyType', jyType);
    next();
  }
});
router.afterEach(function () {
  console.log('router end', new Date().getTime());
});