import axios from 'axios';
import { Toast } from 'vant';
var service = axios.create({
  baseURL: process.env.VUE_APP_BASE_API,
  timeout: 60000
}); // 

service.interceptors.request.use(function (config) {
  // 
  config.headers['Access-Control-Allow-Headers'] = 'x-requested-with,content-type';
  config.headers['Access-Control-Allow-Methods'] = 'POST';
  config.headers['Access-Control-Allow-Origin'] = '*';
  config.headers['Content-Type'] = 'multipart/form-data'; // if (getID()) config.data.bankUserId = getID()
  // if (getToken()) config.data.token = getToken()

  return config;
}, function (error) {
  // do something with request error
  console.log(error); // for debug

  return Promise.reject(error);
}); // 

service.interceptors.response.use(function (response) {
  var res = response.data;

  if (res.code !== 0) {
    // 
    Toast(res.message);
    return Promise.reject(new Error(res.msg || 'Error'));
  }

  return Promise.resolve(res);
}, function (error) {
  console.log("err".concat(error)); // for debug

  Toast(error.message);
  return Promise.reject(error);
});
export default service;