  setNoReadyNumber: function setNoReadyNumber(state, value) {
    if (Array.isArray(value)) {
      state.noReadyNumber = value[0];
    } else {
      state.noReadyNumber += Number(value);

      if (state.noReadyNumber < 0) {
        state.noReadyNumber = 0;
      }
    }
  },
